<template>
  <f7-page name="resolution" ptr @ptr:refresh="pullRefresh">
    <f7-navbar title="Resolutions" back-link="Back">
      <f7-nav-right>
      <f7-link class="searchbar-enable" data-searchbar=".searchbar-demo" icon-ios="f7:search" icon-aurora="f7:search" icon-md="material:search"></f7-link>
    </f7-nav-right>
    <f7-searchbar
      class="searchbar-demo"
      expandable
      :disable-button="!theme.aurora"
      @input="filterSearch"
      @change="disableSearch"
    ></f7-searchbar>
    </f7-navbar>
    <f7-toolbar bottom>
       <f7-link @click="goToPrevPage">
        <i class="icon icon-prev color-white"></i>
      </f7-link>
      <div class="data-table-pagination">
      <span class="data-table-pagination-label">{{((perPage * currentPage) - perPage)+1}}-{{perPage * currentPage}} of {{ totalRows }}</span>
     
    </div>
      <f7-link @click="goToNextPage">
        <i class="icon icon-next color-white"></i>
      </f7-link>
    
  </f7-toolbar>
    <!-- <f7-searchbar
  disable-button-text="Cancel"
  placeholder="Search Barangay/Municipality/City"
  :clear-button="true"
></f7-searchbar> -->

    <!-- <f7-block-title>Resolution Table</f7-block-title> -->

<div class="card data-table">
  <table>
    <thead>
      <tr>
        <th v-for="head in data.headers" :key="head.index"> 
          {{head.text}}
        </th>
        <!-- <th>Actions</th> -->
      </tr>
    </thead>
    <tbody>
      <tr @click="showDetails(index)" v-for="(value,index) in currentPageRows" :key="index">
        <!-- <td class="label-cell">{{index+1}}</td> -->
        <td class="label-cell">{{value.barangay}}</td>
        <td class="label-cell">{{value.municipalitycity}}</td>
        <!-- <td class="label-cell">{{value.particular}}</td>
        <td class="label-cell">{{value.status}}</td> -->
        <td class="label-cell">{{value.datereceived}}</td>
        <!-- <td class="label-cell"></td> -->
      </tr>
      
    </tbody>
  </table>
  
  <!-- <div class="data-table-footer">
    <div class="data-table-rows-select">
      Per page:
      <div class="input input-dropdown">
        <select v-model="perPage" @change="loadcurrentPageRows">
          <option v-for="opt in pageOptions" :key="opt.index">{{opt}}</option>
          <option value="all">All</option>
        </select>
      </div>
    </div>
    <div class="data-table-pagination">
      <span class="data-table-pagination-label">{{((perPage * currentPage) - perPage)+1}}-{{perPage * currentPage}} of {{ totalRows }}</span>
      <a @click="goToPrevPage" href="#" :class="prevArrow">
        <i class="icon icon-prev color-gray"></i>
      </a>
      <a @click="goToNextPage" href="#" :class="nextArrow">
        <i class="icon icon-next color-gray"></i>
      </a>
    </div>
  </div> -->
</div>

  </f7-page>
</template>

<script>
import axios from "axios";
 import { f7,theme } from 'framework7-vue';

export default {
  data(){
    return {
      theme,
        data: [],
        filteredDataValues:[],
        totalRows: 1,
        currentPage: 1,
        totalPages: 1,
        currentPageRows: [],
        perPage: 10,
        pageOptions: [5, 10, 15, 20,50,100],
        prevArrow: 'link disabled',
        nextArrow: 'link',
        searchQuery:'',
        backedupData: [],
        // sortBy: '',
        // sortDesc: false,
        // sortDirection: 'asc',
        // filter: null,
        // filterOn: [],
    }
  },
  mounted(){
    this.loadData()
    
  },

  methods:{
    openPreloader() {
        f7.dialog.preloader();
      },

      closePreloader() {
          f7.dialog.close();
      },
       disableSearch() {
          this.searchQuery = ''
          this.filteredDataValues = []
          this.data =  Object.assign({}, this.backedupData)
          this.loadcurrentPageRows()
      },
    filterSearch:function(e){
      this.searchQuery = e.target.value
    this.filteredDataValues = []
    this.data =  Object.assign({}, this.backedupData)
        if (this.searchQuery != '') {
          console.log('search not empty')
          for (let index = 0; index < this.data.values.length; index++) {
            console.log('start filtering')
            // console.log(this.data.values[index].barangay.toLowerCase())
            if (this.data.values[index].barangay.toLowerCase().includes(this.searchQuery.toLowerCase()) || this.data.values[index].municipalitycity.toLowerCase().includes(this.searchQuery.toLowerCase())) {
            console.log('filtering')
            this.filteredDataValues.push(this.data.values[index]) 
            }
          }
          console.log('finished filtering')
          this.data.values = this.filteredDataValues
          this.totalRows = this.filteredDataValues.length
          console.log(this.data.values)
        }else{
          this.data = this.backedupData
          this.totalRows = this.data.values.length
        }
        this.loadcurrentPageRows()

    },
    showDetails:function(i){
        console.log(this.currentPageRows[i])
        this.createSheet(this.currentPageRows[i])
    },
    createSheet(row) {
        console.log('creating new sheet')
        console.log(row)
        const self = this;

        // Create sheet modal
        if (!self.sheet || self.sheet.destroyed) {
          self.sheet = f7.sheet.create({
            on: {
              close: function () {
                console.log('Closing sheet...');
                setTimeout(() => { self.sheet.destroy(); }); 
              },
              opened: function () {
                console.log('opened sheet modal!');
              }
            },
            content: `
              <div class="sheet-modal">
                <div class="toolbar">
                  <div class="toolbar-inner justify-content-flex-end">
                    <a href="#" class="link sheet-close">Close</a>
                  </div>
                </div>
                <div class="sheet-modal-inner">
                  <div class="page-content">
                    <div class="block">
                      <p><strong>Status</strong>: ` + row.status + `<br>
                      <strong>Barangay</strong>: ` + row.barangay + `<br>
                      <strong>Municipality/City</strong>: ` + row.municipalitycity + `<br>
                      <strong>Date Received</strong>: ` + row.datereceived + `<br>
                      <strong>Particular</strong>: ` + row.particular + `</p>
                    </div>
                  </div>
                </div>
              </div>
            `.trim(),
          });
        }
        // Close inline sheet
        if (f7.$('.demo-sheet.modal-in').length > 0) f7.sheet.close('.demo-sheet');
        // Open it
        self.sheet.open();
      },
      
    goToPrevPage: function(e){
      e.preventDefault();
      if (this.currentPage == 1) {
        this.nextArrow = 'link disable'
      }else{
        this.nextArrow = 'link'
        this.currentPage= this.currentPage-1
      }
      console.log(this.currentPage)
      this.loadcurrentPageRows()
    },
    goToNextPage: function(e){
      e.preventDefault();
      if (this.currentPage == this.totalPages) {
        this.prevArrow = 'link disable'
      }else{
        this.prevArrow = 'link'
        this.currentPage=this.currentPage+1
      }
      console.log(this.currentPage)
      this.loadcurrentPageRows()
    },
    pullRefresh(done) {
        const self = this;

        setTimeout(() => {
          self.loadData()
          done();
        }, 1500);
      },
    loadData: function(){
      
      function replaceAll(string, search, replace) {
        return string.split(search).join(replace);
      }
        var self = this
        self.openPreloader()
        axios.get('https://spreadsheets.google.com/feeds/list/1k5mEtG5MBCkzvnrJuPT8iiXwRMp_HY1hJ_qj4Ja08bo/1/public/values?alt=json')
        .then((response) => {
          // console.log(response.data.feed.entry);
          // console.log(response.status);
          // console.log(response.statusText);
          // console.log(response.headers);
          // console.log(response.config);

          
        var values = response.data;
        var  rows = [];
        var data = {headers:null, values:null};
        var headers = [
          ['Barangay','Municipality/City','Date Received']
        ];

    
              data.headers = headers[0].map(function(header, h) {
                return {
                  text: header,
                  value: replaceAll(header,' ', '').toLowerCase(),
                  hidden: false,
                  align: 'center'
                }
              });  
            
            if (typeof values.feed.entry != 'undefined') {
              for (let index = 0; index < values.feed.entry.length; index++) {

                  if (values.feed.entry[index].gsx$documenttype.$t.toLowerCase().includes('resolution')) { 
                    var status = values.feed.entry[index].gsx$remarksinstructionsactiontaken.$t
                    if (values.feed.entry[index].gsx$statusupdates.$t) {
                      status = values.feed.entry[index].gsx$statusupdates.$t
                    }
                    
                    rows.push({
                      //timestamp: values.feed.entry[index].gsx$timestamp.$t,
                      datereceived: values.feed.entry[index].gsx$datereceived.$t,
                      particular: values.feed.entry[index].gsx$subjectparticular.$t,
                      barangay: values.feed.entry[index]['gsx$origin-barangay'].$t,
                      municipalitycity: values.feed.entry[index]['gsx$origin-municipalitycity'].$t,
                      status: status,
                    
                      //editlink: '',
                    })
                  }
                }
              }
  
              data.values = rows.reverse();   
              
              self.data =  data;

              self.backedupData = Object.assign({}, self.data)

              self.totalRows = self.data.values.length
              console.log(self.totalRows)
              
              self.totalPages =  Math.ceil(self.totalRows / self.perPage)
              console.log(self.totalPages)

              var startIndex = (self.perPage * self.currentPage) - self.perPage
              
              var tempCurrentPageRows = []
              for (let index = startIndex; index < self.data.values.length; index++) {
                if (index < startIndex+self.perPage) {
                  tempCurrentPageRows.push(self.data.values[index])
                }
                
              }
              
              self.currentPageRows = tempCurrentPageRows
              // self.currentPageRows = cloneData.values.splice(startIndex,startIndex + self.perPage)
              self.closePreloader()
              
              
        })
      },
      loadcurrentPageRows: function(){
        var self = this
        self.currentPageRows = []
        var tempCurrentPageRows = []
      
      if (self.data.values) {  
        if (self.perPage.toString().toLowerCase() == 'all') {
          self.currentPageRows = self.data.values
        }else{
          var startIndex = (self.perPage * self.currentPage) - self.perPage
          // console.log('startIndex='+ startIndex.toString())
          // console.log('lastIndex='+ (startIndex+self.perPage).toString())
          
          tempCurrentPageRows = []
          
              for (var index = startIndex; index < self.data.values.length; index++) {
                
                if (index <= (startIndex+self.perPage)) {
                
                  tempCurrentPageRows.push(self.data.values[index])
                }else{
                  break
                }
                
              }
              
              self.currentPageRows = tempCurrentPageRows
        }
        
       
        // self.currentPageRows = cloneData.values.splice(startIndex,startIndex + self.perPage)
        // console.log(startIndex)
        // console.log('here')
        // console.log(self.data.values.length)
        // console.log(self.data.values)
        // console.log(self.data.values[0])
      }
    },
  },
  
}

</script>